@import "vars";
@import "mixins";

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html {
  background-color: $dark-grey;
  font-family: $font;
  font-size: 16px;
}
body {
  font-size: $body-size;
  font-family: $font;
  color: $body-text;
  line-height: 2rem;
  letter-spacing: 1.5px;
  text-shadow: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  opacity: 1;
}
header {
  padding: 6em 1em 1em 0;
  @media screen and (max-width: 736px) {
    padding: 3em 0;
  }
}
h1,
h2,
h3,
h4 {
  margin-bottom: 0.5em;
  font-family: $font;
  opacity: 0.6;
}
li {
  opacity: 0.8;
  & a {
    opacity: 0.9;
  }
}
ul {
  list-style-type: none;
}
p {
  display: inline;
}
a {
  color: $links;
  text-decoration: none;
  box-shadow: 0px 1px 0px 0px $links;
  @include ease(0.4s);
  &:hover {
    color: $links-hover;
    font-style: none;
    box-shadow: 0px 2px 0px 0px $links;
  }
  &:active {
    color: $links;
  }
}
button {
  padding: 6px;
  border: 1px solid #fff;
  border-radius: 5px;
  background-color: transparent;
  font-weight: bold;
  color: #fff;
  line-height: 1em;
  &:hover {
    text-decoration: underline;
  }
}
hr {
  @include skinny-hr;
  opacity: 0.3;
}
nav {
  margin: 1em auto;
}
.wrap {
  margin: 0 auto;
  width: 45%;
  @media screen and (max-width: 1200px) {
    width: 70%;
  }
  @media screen and (max-width: 736px) {
    width: 90%;
  }
}
.tag-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
}
.tag {
  background-color: transparent;
  position: relative;
  bottom: 0;
}
.flex-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.bottom-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.footer {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: transparent;
  position: relative;
  bottom: 0;
  padding: 1rem;
  font-family: $font;
  font-size: 1rem;
  line-height: 1em;
  opacity: 0.8;
  a {
    border-bottom: none;
    display: contents;
  }
}
@import "elements";

// Helpers

.go-left {
  justify-content: flex-start;
  align-items: flex-start;
}
.go-right {
  justify-content: flex-end;
  align-items: flex-end;
}
.go-center {
  justify-content: center;
  align-items: center;
}